<template>
  <li class="playlistitem" @click="naviagteToPlayList(item.id)">
    <div class="thumb">
      <img :src="item.picUrl" alt="" />
      <span class="play-count"> {{ item.playCount | formatPlayCount }}</span>
      <h6>{{ item.name }}</h6>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object
    }
  },
  filters: {
    formatPlayCount: function(value) {
      let k = 10000;
      let sizes = ['', '万', '亿', '万亿'];
      if (value < k) {
        return value;
      } else {
        let i = Math.floor(Math.log(value) / Math.log(k));
        value = (value / Math.pow(k, i)).toFixed(1);
        return value + sizes[i];
      }
    }
  },
  methods: {
    naviagteToPlayList(id) {
      this.$router.push({
        path: '/playlist',
        query: {
          id
        }
      });
    }
  }
};
</script>

<style lang="less">
li.playlistitem {
  flex: 1 1 calc(100vw / 3 - 10px);
  &:nth-of-type(3n + 2) {
    margin-right: 2px;
    margin-left: 2px;
  }
  .thumb {
    position: relative;
    img {
      min-height: 30vw;
      vertical-align: middle;
    }
    .play-count {
      font-size: 12px;
      padding: 3px 3px 0 0;
      position: absolute;
      top: 0;
      right: 0;
      color: white;
      text-shadow: 0 0 1px black;
      &::before {
        content: '';
        background: no-repeat
          url('data:image/svg+xml;base64,');
        background-position: left center;
        background-size: auto 90%;
        width: 0.8em;
        height: 0.9em;
        display: inline-block;
        // vertical-align: middle;
      }
    }
  }
  h6 {
    font-size: 14px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 5px;
    margin-bottom: 10px;
  }
}
</style>
